.smoke-container{height:100vh;background:#000}.smoke-container::before{content:'';position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(to right,red,red,#0f0,#0ff,#ff0,#0ff);mix-blend-mode:color;z-index:1;pointer-events:none}.smoke-container h1{margin:0;padding:0;position:absolute;top:50%;transform:translateY(-50%);width:100%;text-align:center;color:#d1e8e2;font-size:calc(18px + 3vw);letter-spacing:3vw;animation:moveUp .5s ease-out forwards;animation-delay:6s;z-index:2}@media screen and (min-width:800px){.smoke-container h1{font-size:8vw}}.smoke-container h1 span{opacity:0;display:inline-block;animation:animate 1s linear forwards}.smoke-container h1 span:nth-child(1){animation-delay:1.2s}.smoke-container h1 span:nth-child(2){animation-delay:1.7s}.smoke-container h1 span:nth-child(3){animation-delay:2.2s}.smoke-container h1 span:nth-child(4){animation-delay:2.7s}.smoke-container h1 span:nth-child(5){animation-delay:3.2s}.smoke-container h1 span:nth-child(6){animation-delay:3.7s}.smoke-container h1 span:nth-child(7){animation-delay:4.2s}.smoke-container h1 span:nth-child(8){animation-delay:5s}.smoke-container h1 span:nth-child(9){animation-delay:5s}.smoke-container h1 span:nth-child(10){animation-delay:5s}.smoke-container h1 span:nth-child(11){animation-delay:5s}.smoke-container h1 span:nth-child(12){animation-delay:5s}.smoke-container h1 span:nth-child(13){animation-delay:5s}.smoke-container h1 span:nth-child(14){animation-delay:5s}.smoke-container h1 span:nth-child(15){animation-delay:5s}.smoke-container h1 span:nth-child(16){animation-delay:5s}.smoke-container video{margin:0;padding:0;position:absolute;top:50%;transform:translateY(-50%);max-width:100%}.smoke-container .intro{opacity:0;position:absolute;top:50%;margin:0;padding:0;transform:translateY(-50%);width:100%;z-index:2;color:#d1e8e2;text-align:center;animation:slow-show 1s linear forwards;animation-delay:6.5s;font-size:calc(14px + 2vw)}@media (min-width:800px){.smoke-container .intro{top:75%}}.smoke-container .start{opacity:0;position:absolute;top:90%;margin:0;padding:0;transform:translateY(-50%);width:100%;z-index:2;color:#d1e8e2;text-align:center;animation:slow-show 2s linear forwards;animation-delay:7s}.smoke-container .start:hover{color:orange}.smoke-container .start:hover .arrow::after{top:-90%;transform:rotate(45deg);border-color:orange;height:120%}.smoke-container .start:hover .arrow::before{border-color:orange;transform:scale(.8)}.smoke-container .start .arrow{width:6vmin;height:6vmin;box-sizing:border-box;position:absolute;left:50%;top:50%;transform:rotate(135deg)}.smoke-container .start .arrow::before{content:'';width:100%;height:100%;border-width:.8vmin .8vmin 0 0;border-style:solid;border-color:#d1e8e2;transition:.2s ease;display:block;transform-origin:100% 0}.smoke-container .start .arrow:after{content:'';float:left;position:relative;top:-100%;width:100%;height:100%;border:0 solid #d1e8e2;border-right-width:.8vmin;transform-origin:100% 0;transition:.2s ease}@keyframes moveUp{0%{top:50%}100%{top:30%}}@keyframes slow-show{0%{opacity:0}100%{opacity:1}}@keyframes animate{0%{opacity:0;transform:rotateY(90deg);filter:blur(10px)}100%{opacity:1;transform:rotateY(0);filter:blur(0)}}